@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
button{all:unset;text-align:center;font-size:19px;font-weight:bold;cursor:pointer;width:60px;height:60px;border-radius:8px;box-shadow:2px 2px 1px 2px #ecf2f7;font-weight:700;transition:.2s ease-out}button:active{margin:2px 2px 0px 0px;box-shadow:none}.equals{width:140px;grid-column:3/5;background-color:#f5cc16}.equals:hover{background-color:#1c9950;transition:.1s ease-out}
.buttonBox{width:100%;height:calc(90% - 130px);display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:20px}
.screen{height:180px;padding:0 10px;width:100%;margin-bottom:10px;display:flex;align-items:flex-end;justify-content:flex-end}
.wrapper{width:350px;height:650px;padding:20px;border-radius:25px;background-color:#fff;box-shadow:5px 5px 3px 3px #dde7ee}
*{box-sizing:border-box;margin:0;padding:0}html,body{display:flex;justify-content:center;align-items:center;width:100vw;height:100vh;box-sizing:border-box;background-color:#edf1f3;font-family:"Noto Sans KR",sans-serif}
